body {
  height: 100vh;
  background-color: #f5f5f5;
  background-image: url("mathematics.4a73ea2d.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.calculator {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 20px;
}

.display {
  width: 100%;
  text-align: right;
  box-sizing: border-box;
  margin-bottom: 10px;
  padding: 5px;
}

.buttons {
  grid-template-columns: repeat(4, 1fr);
  gap: 5px;
  display: grid;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px;
}

button:hover {
  background-color: #0069d9;
}

.clear, .equals {
  background-color: #dc3545;
  grid-column: span 2;
}

.clear:hover, .equals:hover {
  background-color: #c82333;
}

.image {
  max-width: 100%;
  margin-top: 20px;
  display: block;
}

